
import listItem from '@/components/listItem'
import ShopBtn from '@/components/shopBtn'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import { submitCommentInfo } from '@/api/user'
import { uploadFile, uploadWechatFile } from '@/api/other.js'

export default {
name: 'uc-submitcomments',
components: {
listItem,
Group,
Cell,
FormPreview,
XTextarea,
ShopBtn,
Checklist,
Rater
},
data () {
return {
localIds: [], // 微信图片id
serverId: [],
raterNum: '0',
productData: [],

picArr: [],
picArrList: [],
postData: {
id: '',
pics: '',
point: 5,
txt: ''
}
}
},
computed: {
listData () {
return this.productData
}
},
created () {
const item = JSON.parse(decodeURIComponent(this.$route.query.item))

let newObj = {
img: item.skuimg,
titL: item.productname,
titR: '￥' + item.price,
subtitL: item.skuname,
subtitR: '×' + item.num
}
this.productData.push(newObj)
this.postData.id = item.skuid
},
methods: {
chooseImg () {
console.log(this.$wechat)
const that = this
this.$wechat.chooseImage({
count: 3 - that.picArr.length, // 默认9

sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: function (res) {
that.picArr = that.picArr.concat(res.localIds)
that.localIds = that.picArr
console.log(JSON.stringify(res.localIds))
}
})
},

syncUpload () {
if (!this.localIds.length) {


this.syncSubmit()
} else {
const that = this
const localId = this.localIds.pop()
that.$wechat.uploadImage({
localId: localId,
isShowProgressTips: 0,
success: function (res) {
that.serverId.push(res.serverId)
that.syncUpload()
}
})
}
},

syncSubmit () {

if (!this.serverId.length) {


submitCommentInfo(this.postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('评论成功！')
this.$router.replace({
name: 'uc-comments',
query: {
type: 1
}
})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
console.log(this.postData.pics)
} else {
const that = this
const serverId = this.serverId.pop()
that.handleUpload(serverId)
}
},

handleSubmit () {
if (this.postData.txt === '') {
this.$vux.toast.text('请填写评论内容')
return
}
this.$vux.loading.show()
if (this.picArr.length > 0) {


this.postData.pics = ''
const that = this
that.syncUpload()
} else {
submitCommentInfo(this.postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('评论成功！')
this.$router.replace({
name: 'uc-comments',
query: {
type: 1
}
})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
}
},

handleUpload (val) {
const postData = {
mediaid: val
}
uploadWechatFile(postData)
.then(res => {


if (res.data.errorcode === 0) {



this.postData.pics = this.postData.pics ? this.postData.pics + ',' + res.data.data.filePath : res.data.data.filePath
this.syncSubmit()

} else {
this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
}
})
.catch(err => {
console.log(err)
this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
})
},

fileChange (obj) {
const file = obj.target.files[0]

if (String(file.type.toUpperCase()) === 'IMAGE/PNG' || String(file.type.toUpperCase()) === 'IMAGE/JPEG') {
console.log('continue')
} else {
this.$vux.toast.text('只支持PNG/JPEG格式的图片')
return
}

if ((file.size / 1024) > 3072) {
this.$vux.toast.text('最大只支持3M的图片')
return
}

if (this.picArr.length > 3) {
this.$vux.toast.text('最多3张图片')
return
}
if (this.picArr.length === 2) {
this.$vux.toast.text('最多上传3张图片')
return
}

console.log('开始上传图片')
this.$vux.loading.show()

let param = new FormData() 
param.append('file', file, file.name) 
console.log(param.get('file'))
uploadFile(param)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {



this.picArr.push(res.data.data.filePath)
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleDelImg (index) {
this.picArr.splice(index, 1)
this.localIds = this.picArr
console.log(this.picArr)
},
change (val, label) {
console.log('change', val, label)
}
}
}



